@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
* {
  font-family: "Inter";
}

html {
  scroll-behavior: smooth;
  background-color: #222222;
}

body {
  background: linear-gradient(135deg, rgba(0,71,62,1) 0%, rgba(0,68,2,1) 100%);
  color: white;
  margin: 0;
}

.view-area {
  max-width: 1150px;
	margin: 0 auto !important;
	float: none !important;
  padding: 0em 1em;
}

h1 {
  font-weight: 900;
}

a {
  color: #CCCCCC;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.flex-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 2em;
  gap: 2em;
}

.flex-container img {
  max-width: 575px;
  height: 100%;
  max-height: 80vh;
  transition: 0.5s all ease-in-out;
}

.flex-container .image-box {
  overflow: hidden;
  display: flex;
  filter: drop-shadow(0 0 0.25em black);
  border-radius: 10px;
  transition: 0.5s all ease-in-out;
  margin: 0;
}

.flex-container .image-box:hover {
  filter: drop-shadow(0 0 0.5em black);
}

.flex-container img:hover {
  transform: scale(1.3);
}

.flex-container--flipped {
  flex-direction: row-reverse;
}

.link-button {
  padding: 1.5em 1em;
  margin: 0.25em 0em;
  background-color: rgba(255,255,255,0.2);;
  color: white;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  transition: background-color 0.25s, color 0.25s;
  border-radius: 128px;
}

.link-button:hover {
  text-decoration: none;
  background-color: white;
  color: black;
}

.link-button:active {
  text-decoration: none;
  background-color: rgba(255,255,255,0.6);
  color: black;
}

.button-wrapper {
  display: flex;
  justify-content: space-around;  
}

.section-header {
  font-size: 3.5em;
  margin: 0;
}

.contact-container-background {
  background-image: url(/home/static/media/code_pic1.4a8ce595.jpg);
}

@media screen and (max-width: 700px) {
  .view-area {
    max-width: 500px;
  }

  .flex-container {
    flex-direction: column;
  }

  .flex-container img {
    max-width: 100%;
    max-height: 100vh;
  }

  .section-header {
    text-align: center;
  }

  .section-subheader {
    text-align: center;
  }
}
.navbar-container__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5em;
  font-weight: 500;
}
  
.navbar-container__content h1 {
  font-weight: 700;
  text-align: center;
}
  
.navbar-container__content a {
  color: white;
  text-decoration: none;
}
  
.navbar-container__content__links {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: flex;
}
  
.navbar-container__content__links li {
  float: right;
}
  
.navbar-container__content__links li a {
  display: block;
  text-align: center;
  padding: 14px 16px;
  transition: 0.25s all ease-in-out;
  border-radius: 32px;
}
  
.navbar-container__content__links li a:hover {
  background-color: rgba(0,0,0,0.3);
}
  
.navbar-container__content__links li a:active {
  background-color: rgba(0,0,0,0.5);
}

#skip-nav-link {
  position: absolute;
  transform: translateY(-500%);
  transition: transform 200ms;
  background-color: white;
  color: black;
}

#skip-nav-link:focus {
  transform: translateY(0);
}

@media screen and (max-width: 700px) {
  .navbar-container__content {
    flex-direction: column;
    height: auto;
  }

  .navbar-container__content__links li {
    float: left;
  }
}
.flex-container--about {
  min-height: calc(100vh - 7em);
  padding: 1em 0em;
}

.flex-container--about img {
  height: 100%
}

@media screen and (max-width: 700px) {
  .flex-container--about {
    flex-direction: column-reverse;
  }
}
.example-container {
  margin: 1em 0em;
}

.example-container-container h1 {
  font-weight: 700;
}

.example-container h2 {
  font-weight: 500;
}

.example-container img {
  filter: brightness(1) blur(0px);
  transition: 0.5s all ease-in-out;
  min-width: 50%;
  height: auto;
}

.example-container img:hover {
  filter: brightness(1.25);
}

.example-container img:active {
  filter: brightness(0.5) blur(16px);
  transition: 0.1s filter ease-in-out;
}
.works-container__content {
  padding: 0;
}

.works-container {
  min-height: calc(100vh - 2em);
  padding: 1em 1em;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.3);
}
.map {
  width: 575px;
  height: 70vh;
  max-height: 575px;
  max-width: calc(100vw - 575px)
}

@media screen and (max-width: 700px) {
  .map {
    width: calc(100vw - 2em);
    height: 90vh;
    max-width: 100%;
  }
}
.contact-container-background {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contact-container {
  background-color: rgba(0,0,0,0.5);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

.contact_container__content .flex-container {
  min-height: calc(100vh - 6em);
  padding: 1em 0em;
  width: 100%;
}

.contact-location-info {
  text-align: center;
}

.email-copier-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 1em;
  border-radius: 64px;
  background-color: rgba(255,255,255,0.2);
  color: white;
  transition: background-color 0.25s;
  min-width: 10em;
}

.email-copier-container:hover {
  background-color: white;
}

.email-copier-container:hover .email-copier-container__text {
  color: black;
}

.email-copier-container:hover .email-copier-container__icon i {
  color: black;
}

.email-copier-container:active .email-copier-container__text {
  color: black;
}

.email-copier-container:active .email-copier-container__icon i {
  color: black;
}

.email-copier-container:active {
  background-color: rgba(255,255,255,0.8);
}

.email-copier-container__text {
  line-height: 6.5em;
  color: white;
  box-shadow: none;
  border-width: 0px;
  padding: 0em 1em;
  width: 100%;
  background-color: transparent;
  /* border-radius: 64px; */
  transition: background-color 0.25s, color 0.25s;
}

.email-copier-container__text:hover {
  outline:none!important;
  background-color: transparent;
  color: black;
}

.email-copier-container__text:focus {
  outline:none!important;
  background-color: transparent;
  color: black;
}

.email-copier-container__icon {
  padding: 10px;
  color: white;
  border-radius: 256px;
}

.email-copier-container__icon i {
  transition: color 0.25s;
}

.contact-info-container {
  flex-grow: 2;
}

.contact-info-container__links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.button-wrapper--contact-info {
  flex-wrap: wrap;
}

.contact-info-container__links__priority-links {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-size: 1.5em;
  grid-gap: 1em;
  gap: 1em;
}

.link-button--resume {
  min-width: 10em;
  padding: 1.25em 0em;
}

@media screen and (max-width: 700px) { 
  .contact_container__content .flex-container {
    flex-direction: column-reverse;
  }
  
  .contact-info-container {
    min-width: 100%;
  }
  
  .link-button--resume {
    min-width: 100%;
  }
  
  .contact-info-container__links__priority-links {
    flex-direction: column;
    grid-gap: 0em;
    grid-gap: 0em;
    gap: 0em;
  }
  
  .email-copier-container {
    min-width: 100%;
  }
  
  .email-copier-container__text {
    width: 100%;
    font-size: 1em;
    line-height: 4em;
  }
  
  .contact-info-container__links {
    grid-gap: 2em;
  }
  
  .button-wrapper--contact-info {
    flex-direction: column;
  }
}
.footer-container {
  background-image: linear-gradient(#111111, #222222);
}

.footer-container__content {
  padding-top: .5em;
  padding-bottom: .5em;
  text-align: center;
  height: 4em;
  line-height: 4em;
}

.footer-container__content h2 {
  margin: 0;
}

.react-link__icon {
  transition: transform 0.5s;
  margin: 0em 0.2em;
}

.react-link:hover .react-link__icon {
  transform: rotate(180deg);
}

@media screen and (max-width: 700px) {
  .footer-container__content {
    height: auto;
    line-height: 2em;
  }
}
